:root {
  --dark-mode-bg: #333;
  --dark-mode-ui: #555;
  --dark-mode-text: #ddd;
  --light-mode-bg: #f3f3f3;
  --light-mode-ui: #fafafa;
  --light-mode-text: #333;
  --theme-bg: var(--light-mode-bg);
  --theme-ui: var(--light-mode-ui);
  --theme-text: var(--light-mode-text); }

.App,
.footer,
.sidenav {
  background-color: #f3f3f3;
  background-color: var(--theme-bg);
  color: #333;
  color: var(--theme-text); }

.card,
.footer,
.sidenav {
  background-color: #fafafa;
  background-color: var(--theme-ui); }

input, .sidenav li > a {
  color: #333;
  color: var(--theme-text); }

* {
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.validate-error {
  border-radius: 5px !important;
  box-shadow: 0 0 8px red !important; }

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }
  .App main {
    flex: 1 1; }

.row {
  margin-bottom: 0 !important; }

nav .brand-logo {
  font-size: 1.6rem !important; }

#mobile-links {
  padding-top: 8vh; }
  #mobile-links li a {
    font-size: 1.5rem; }

#to-top-btn {
  position: fixed;
  bottom: 20px;
  right: 20px; }
  #to-top-btn i {
    font-size: 2em; }

.switch label input[type="checkbox"] + .lever {
  background-color: #faecd7 !important; }
  .switch label input[type="checkbox"] + .lever:after {
    background-color: #ffa726 !important; }

.switch label input[type="checkbox"]:checked + .lever {
  background-color: #faecd7 !important; }
  .switch label input[type="checkbox"]:checked + .lever:after {
    background-color: #ffa726 !important; }

#edit-button {
  position: fixed;
  bottom: 20px;
  left: 20px; }
  #edit-button i {
    font-size: 2em; }

.noUi-horizontal {
  height: 12px;
  background-color: #faecd7; }
  .noUi-horizontal .noUi-connect {
    background-color: #ffb84e; }
  .noUi-horizontal .noUi-handle {
    width: 22px;
    height: 22px;
    right: -11px !important;
    border-radius: 50%;
    background-color: #ffa726 !important;
    box-shadow: none; }
    .noUi-horizontal .noUi-handle:before, .noUi-horizontal .noUi-handle:after {
      content: none; }
  .noUi-horizontal .noUi-marker-large {
    height: 10px; }
  .noUi-horizontal .noUi-value-large {
    font-size: 0.9em; }
  .noUi-horizontal .noUi-tooltip {
    display: none; }
  .noUi-horizontal .noUi-active .noUi-tooltip {
    display: block; }

#time-slider-sleep.noUi-horizontal {
  background-color: #ffb84e !important; }
  #time-slider-sleep.noUi-horizontal .noUi-connect {
    background-color: #faecd7 !important; }

#timezone-checkbox {
  display: flex;
  margin-top: 15px; }
  #timezone-checkbox p {
    margin: 0; }
  #timezone-checkbox #timezone-checkbox-span {
    padding: 0; }

#clock #clock-time {
  margin: 0; }

#clock #clock-date {
  margin: 0; }

#main-day-icon {
  position: absolute;
  top: 80px;
  right: 20px;
  font-size: 3rem; }

.card {
  width: 100%;
  border-radius: 8px; }

.footer {
  height: 60px;
  width: 100%;
  margin-top: 20px;
  border-top: solid 1px #999; }
  .footer h6 {
    width: 100%;
    margin: 0; }

.friend-details i:not(.i-edit-button) {
  padding-top: 8px !important; }

.friend-details p {
  font-size: 1.3rem;
  margin: 5px 0; }

